import React from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Grid,
  colors,
} from '@material-ui/core';

import {
  TrackingChangeModel,
} from '../services/common';

import {
  ClusterViewModel,
} from '../services/cluster';

const getRowColor = (status: number): string => {
  switch (status) {
    case 1: return colors.green[500];
    case 2: return colors.amber[500];
    default: return colors.red[500];
  }
}

type Props = {
  data: TrackingChangeModel<ClusterViewModel>[];
}

export default function ClustersChangeTable({
  data,
}: Props): JSX.Element {
  return (
    <Table stickyHeader aria-label="sticky table" size="small">
      <TableHead>
        <TableRow>
          <TableCell align="left" style={{ fontSize: '0.75rem' }}>M cu kin</TableCell>
          <TableCell align="left" style={{ fontSize: '0.75rem' }}>Din gii</TableCell>
          <TableCell align="left" style={{ fontSize: '0.75rem' }}>Ghi ch</TableCell>
          <TableCell align="left" style={{ fontSize: '0.75rem' }}>Chit tnh</TableCell>
          <TableCell align="left" style={{ fontSize: '0.75rem' }}>S lng</TableCell>
          <TableCell align="left" style={{ fontSize: '0.75rem' }}>Cng tc</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {data.map((item, index) => (
          <TableRow key={index} style={{ backgroundColor: getRowColor(item.status) }}>
            <TableCell align="left" style={{ fontSize: '0.75rem' }}>
              <Grid container direction="column">
                <Grid item>
                  {item.post !== null ? item.post.code : '-'}
                </Grid>
                <Grid item>
                  {item.pack !== null ? item.pack.code : '-'}
                </Grid>
              </Grid>
            </TableCell>
            <TableCell align="left" style={{ fontSize: '0.75rem' }}>
              <Grid container direction="column">
                <Grid item>
                  {item.post !== null ? item.post.description : '-'}
                </Grid>
                <Grid item>
                  {item.pack !== null ? item.pack.description : '-'}
                </Grid>
              </Grid>
            </TableCell>
            <TableCell align="left" style={{ fontSize: '0.75rem' }}>
              <Grid container direction="column">
                <Grid item>
                  {item.post !== null ? item.post.note : '-'}
                </Grid>
                <Grid item>
                  {item.pack !== null ? item.pack.note : '-'}
                </Grid>
              </Grid>
            </TableCell>
            <TableCell align="left" style={{ fontSize: '0.75rem' }}>
              <Grid container direction="column">
                <Grid item>
                  {item.post !== null ? item.post.filePath : '-'}
                </Grid>
                <Grid item>
                  {item.pack !== null ? item.pack.filePath : '-'}
                </Grid>
              </Grid>
            </TableCell>
            <TableCell align="right" style={{ fontSize: '0.75rem' }}>
              <Grid container direction="column">
                <Grid item>
                  {item.post !== null ? item.post.elementCount : '-'}
                </Grid>
                <Grid item>
                  {item.pack !== null ? item.pack.elementCount : '-'}
                </Grid>
              </Grid>
            </TableCell>
            <TableCell align="right" style={{ fontSize: '0.75rem' }}>
              <Grid container direction="column">
                <Grid item>
                  {item.post !== null ? item.post.itemCount : '-'}
                </Grid>
                <Grid item>
                  {item.pack !== null ? item.pack.itemCount : '-'}
                </Grid>
              </Grid>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
}
